.service.container{
    flex-direction: column;
}

.service-items-container{
    display: flex;
    flex-direction: column;
    width: 90%;
    margin: 0 auto;
}

.service-item{
    width: 48%;
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-start;
    background: #1E1E1E;
    padding: 4.2% 3%;
}


.service-upper-col .service-item:first-child{
    margin: 0;

}

.service-items-container .service-upper-col{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
}

.service-item .service-title{
    /* font-size: 2.3vw; */
    font-size: 1.9rem;
    line-height: 1em;
}

.service-item.lower{
    width: 100%;   
    margin-top: 5%;
}


.service-item .service-text{
    /* padding-right: 5vw; */
    margin-top: 1.3rem;
    font-size: 1.6rem;
    /* width: 80%; */
}

@media (max-width: 1400px) {
    .service-item .service-text{
        font-size: 1.3rem
    }
    .service-item .service-title{
        font-size: 1.6rem;
    }
}

@media (max-width: 600px){
    .service-items-container{
        width: 100%;
        margin: 0;
    }

    .service-items-container .service-upper-col{
        flex-direction: column;
    }

    .service-items-container .service-item{
        width: 100%;
        margin-top: 5%;
        padding: 5% 2%;
    }

    .service-item.lower{
        margin-top: 5%;
    }

    .service-item .service-title{
        font-size: 1.4rem;
    }

    .service-item .service-text{
        font-size: 1rem;
        width: 100%;
    }

}